.signup-container {
    height: 100%;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.signup-box {
    /* background-color: var(--clr-para); */
    width: 90%;
    max-width: 700px;
    /* height: 500px; */

    display: flex;
}

.signup-box>div:nth-child(1) {
    flex: 2;
    background-image: url('../../media/login-banner.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    /* border: .1rem solid var(--clr-subhead); */
    /* border-right: none; */
}

.signup-box>div:nth-child(2) {
    background-color: var(--clr-bg);
    flex: 3;
    /* border: .1rem solid var(--clr-subhead); */
    /* border-left: none; */

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10%;
}

.signup-form>div {
    margin-bottom: 1rem;
}

.signup-form>form {
    width: 100%;
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
}

.signup-form>form>div {
    position: relative;
}

.signup-form>form>div>label {
    background-color: var(--clr-bg);
    color: var(--clr-head);
    font-size: .9em;
    margin-left: 1rem;
    padding: .1rem .3rem;
    /* background-color: var(--clr-para); */

    position: absolute;
    top: -.6rem;
}

.signup-form>form>div>input {
    background-color: var(--clr-bg);
    color: var(--clr-head);
    width: 100%;
    padding: 0.6rem 1rem;
    font-size: 1em;
    border: .1rem solid var(--clr-head);
    border-radius: 5px;
    outline: none;
}

.signup-form>form>button {
    width: max-content;
    align-self: flex-end;
    margin-top: 1.5rem;
}


@media all and (max-width:650px) {
    .signup-banner {
        display: none;
    }
}