.app-container {
    background-color: var(--clr-bg);
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    display: flex;
    flex-direction: column;
}

.app-box {
    /* background-color: green; */
    flex: 1;
    width: 100%;
    height: 100%;

    display: flex;
}

.main-container {
    /* background-color: yellow; */
    flex: 1;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}