.sidebar-container {
    background-color: var(--clr-head);
    width: 200px;
    height: 100%;
    padding: 2rem;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    overflow-y: scroll;
    color: var(--clr-bg);
}


@media all and (max-width: 700px){
    .sidebar-container {
        display: var(--display);
        width: 100%;
    }
}

