.nav-container {
    background-color: var(--clr-head);
    width: 100vw;

    display: flex;
    justify-content: space-between;
}

.nav-container>div:nth-child(1) {
    padding: .5rem 1rem;
    display: flex;
    gap: .7rem;
    align-items: center;
}

.nav-logo>img:nth-child(1) {
    width: 1.2rem;
    display: none;
    margin-right: 1rem;
  } 

.nav-logo>img {
    width: 2.5rem;
}

.nav-container>div:nth-child(2) {
    padding: 1rem;
}

/* -------------- OPTIONS ----------- */
.nav-container>div:nth-child(2) {
    padding: 1rem 2rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}

.nav-options>img {
    width: 1.8rem;
    margin-right: -.5rem;
}

.nav-options>button {
    background-color: var(--clr-head);
    border: .1rem solid var(--clr-bg);
    color: var(--clr-bg);
    padding: 0.3rem .8rem;
    border-radius: 5px;
}

@media all and (max-width: 700px) {
  .nav-logo>img:nth-child(1) {
    display: inline;
  }  
}